export function initTijeraEl() {
    class TijeraComp extends HTMLElement {
        constructor() {
            super();
            this.render();

        }
        render() {
            const tijera = require("url:../../../src/img/tijera.svg");
            const variant = this.getAttribute("variant") || "small"
            const shadow = this.attachShadow({ mode: 'open' });
            const div = document.createElement("div");
            const style = document.createElement("style");
            div.innerHTML = `
                <img variant="big" class="tijera" src=${tijera}> 
            `
            style.innerHTML = `
                .big{
                    width: 103px;
                    height: 235px;
                }
                .small{
                    width: 56px;
                    height: 126px;
                }
                .tijera{
                    width:100%;
                }
            `

            const elegido = div.querySelector(".tijera");
            elegido.addEventListener("click", (r) => {
                const evento = new CustomEvent("change", {
                    detail:
                    {
                        myPlay: "tijera"
                    }
                })
                this.dispatchEvent(evento);
            })
            div.className = variant;
            shadow.appendChild(div);
            shadow.appendChild(style);
        }
    }

    customElements.define("tijera-comp", TijeraComp);
}
